@import "https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@400;700&family=Merriweather:wght@400;700&family=Crimson+Text:wght@400;700&display=swap";
body {
  color: #0000;
  background-color: #d1b899;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

main {
  background-color: #d1b899;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem;
}

.movie-photo {
  object-fit: cover;
  filter: grayscale(75%);
  border-radius: 15px;
  justify-content: center;
  width: 100%;
  height: 450px;
  padding: .5rem;
  display: flex;
  position: relative;
}

.movie-div {
  width: 100%;
  max-width: 1200px;
  height: auto;
  position: relative;
  overflow: hidden;
}

.movie-content {
  transition: transform .5s;
  display: flex;
}

.movie {
  box-sizing: border-box;
  min-width: 100%;
}

.nav-btn {
  cursor: pointer;
  color: #d1b899;
  z-index: 1;
  background-color: #0000;
  border: none;
  font-size: 30px;
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
}

#prev {
  left: 15px;
}

#next {
  right: 15px;
}

@media (width <= 768px) {
  #movie-div {
    padding: 1rem;
  }

  .movie-photo {
    height: 300px;
  }

  .nav-btn {
    font-size: 24px;
  }

  main {
    padding: .5rem;
  }
}

@media (width >= 601px) and (width <= 900px) {
  .movie-photo {
    height: 450px;
  }

  .nav-btn {
    font-size: 26px;
  }

  main {
    padding: 1rem;
  }
}

@media (width >= 901px) {
  .movie-photo {
    height: 450px;
  }

  .nav-btn {
    font-size: 30px;
  }

  main {
    padding: 1rem 2rem;
  }
}

@media (width >= 1201px) {
  .movie-photo {
    height: 500px;
  }

  .nav-btn {
    font-size: 32px;
  }

  main {
    padding: 2rem;
  }
}
